html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body,html,#root,.wrapper{height:100%}

.button--large{height:56px !important}

.block{padding:24px;background:#ffffff;box-shadow:0px 0px 25px rgba(0,0,0,0.04204);border-radius:3px}

.auth__top{text-align:center}

.auth{display:flex;justify-content:center;align-items:center;height:100%}.auth .block{max-width:430px;width:100%}.auth__success-block{text-align:center;display:flex;align-items:center;flex-direction:column;justify-content:center;height:400px}.auth__success-block svg{width:48px;height:48px}.auth__success-block h2{margin-top:20px}.auth__success-block p{color:#adadad}.login-form{display:flex;flex-direction:column;align-items:center}


.message{display:flex}.message__info{display:flex;margin-bottom:10px}.message__avatar{display:flex;flex-direction:column-reverse;max-width:440px}.message__avatar img{border-radius:50px;width:33px;height:33px;margin-right:13px;margin-top:23px}.message__bubble{background:#3674FF;box-shadow:0px 5px 5px rgba(54,116,255,0.196);border-radius:12px 12px 12px 0;padding:15px;margin-bottom:8px}.message__date{font-size:12px;opacity:0.4}.message__text{color:#fff;line-height:20px}.message__attachments{display:flex}.message__attachments-item{margin:15px}.message__attachments-item img{width:45px;height:auto;border-radius:4px}.message--isme{flex-direction:row-reverse}.message--isme .message__info{flex-direction:row-reverse}.message--isme .message__avatar img{margin-right:0;margin-left:13px}.message--isme .message__info{text-align:right}.message--isme .message__bubble{background:#ffffff;border:1px solid #ececec;box-shadow:0 4px 4px rgba(0,0,0,0.0220444);border-radius:12px 12px 0px 12px}.message--isme .message__bubble .message__text{color:#000}

